html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  line-height: 1.4;
  font-family: 'Vollkorn', serif;
  color: #fff;
  overflow: hidden;
  height: 100vh;
}

.header {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  padding: 20px;
  font-family: 'Amatic SC', cursive;
  z-index: 1500;
}

img {
  max-width: 100%;
  display: block;
}

.pagination {
  position: fixed;
  bottom: 50px;
  left: 50px;
  z-index: 1500;
}

.pagination li {
  margin-bottom: 10px;
}

.pagination a {
  width: 10px;
  height: 10px;
  display: block;
  background: #fff;
  border-radius: 50%;
}

.pagination a.is-active {
  background: red;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 100vh;
}

.section {
  height: 100vh;
  width: 100%;
  background: #000;
  padding: 10vw;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

.section.is-active {
  opacity: 1;
}

.section:nth-child(2) {
  background: #2ecc40;
}

.section:nth-child(3) {
  background: #00d0a0;
}

.section:nth-child(4) {
  background: #7fdcff;
}

.section h2 {
  font-family: 'Amatic SC', cursive;
  font-size: 80px;
  margin-bottom: 1em;
  position: relative;
}

.section h2 span {
  position: absolute;
  left: -.7em;
}

.section p {
  display: inline-block;
  width: 30%;
  font-size: 20px;
  padding-right: 20px;
  line-height: 1.6;
  vertical-align: top;
}

#waves {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 300;
  pointer-events: none;
}

#waves .waves__01 {
  fill: #fec300;
}

#waves .waves__02 {
  fill: #bccf3c;
}
